@keyframes avatar_Blink { 
  50% {border:5px yellow dashed;border-radius:15px} 
}

@keyframes button_Blink{
  50%{
    color:lightgrey;
  }
}
@keyframes button_Blink_Hover{
  50%{
    color:white;
  }
}
@keyframes fadein{
  from{
    opacity:0
  }
  to{
    opactiy:1
  }
}

.board{
  display:flex;
  justify-content: center;
  margin:0 auto;
}
.game {
  margin: 0;
  width:450px;
  min-height:600px;
  justify-content:center;
  background:blue;
  border: dashed black 5px; 
  font-family: "Press Start 2P", cursive;
  animation: 2s fadein ease-in;
  display:block;
  position:absolute;
}
.game-title{
  color:yellow;
  text-decoration:underline;
}
.startMenu_Top{
  display:flex;
  flex-direction:column;
  align-items:center;
  min-height:400px;
}
.startMenu_Bottom{
  min-height:150px;
  display:block;
  display:flex;
  flex-direction:column-reverse;
  align-items:center;
  justify-content: flex-end;
}

.window{
  width:300px;
  height:250px;
  border: white 5px dashed;
}

.text{ 
 color:white;
 font-size:.75em;
 padding:8px;
}

Button,.button{
  font-family: "Press Start 2P", cursive;
  font-size:1em;
   border:black 5px dashed;
   margin:10px;
   background-color:lightgray;
   animation:button_Blink 1s step-end infinite alternate;
}


Button:hover{
  color:black;
  cursor: pointer;
  border:5px yellow dashed;
  background-color:lightgray;
  animation:button_Blink_Hover 1s step-end infinite alternate;
}

Input{
  font-family: "Press Start 2P", cursive;
  font-size:1em;
   border:black 3px dashed;
   margin:10px;
  }
  
.displayNone{
  display:none;
}



.avatarsContainer{
  display:flex;
}


.avatar{
  width:100px;
  height:100px;
  margin:10px;
  border-radius: 15px;
  border:white 5px dashed;
  outline:none;
}
.avatar:hover{
  cursor: pointer;
}
.selected{
  animation: avatar_Blink .3s step-end infinite alternate;
}

.finalized{
  color:yellow;
  display:flex;
  justify-content: center;
  align-items:center;
  flex-direction:column;
}
.finalizedImage{
  width:250px;
  height:250px;
  margin:10px;
  border-radius: 15px;
  border:white 5px dashed;
  outline:none;
}


.state{
  color:white;
}